import {chunk, range} from "lodash";
import moment from "moment";

export type IterableMonth = number[][];
export interface CalendarDate {
    year: number;
    month: number;
    day: number;
}

export function getIterableMonth(year: number, month: number): IterableMonth {
    const daysCount = getDaysInMonth(year, month);
    const days = range(1, daysCount + 1);
    const firstDayOfWeek = getDayInWeek(year, month, days[0]);
    const lastDayOfWeek = getDayInWeek(year, month, days[days.length - 1]);

    const n = range(0, firstDayOfWeek).fill(0);
    const t = [...n, ...days];
    const s = [...t, ...range(6 - lastDayOfWeek).fill(0)];

    return chunk(s, 7);
}

export function getWeekdays() {
    const [first, ...weekdays] = range(7).map(index => moment(index, 'e').format('dd'));
    return [...weekdays, first];
}

export function getDayInWeek(year: number, month: number, day: number): number {
    const dayOfTheWeek = new Date(year, month, day).getDay();

    return dayOfTheWeek === 0 ? 6 : dayOfTheWeek - 1;
}

export function getDaysInMonth(year: number, month: number): number {
    return new Date(year, month + 1, 0).getDate();
}

export function getMonthNameByIndex(index: number) {
    return moment(new Date(2000, index + 1, 0)).format('MMMM');
}

export function getWeeksCountInMonth(year: number, month: number): number {
    const daysCount = getDaysInMonth(year, month);
    const firstDay = getDayInWeek(year, month, 1);
    const lastDay = getDayInWeek(year, month, daysCount);

    return (daysCount - (7 - firstDay) - (lastDay + 1)) / 7 + 2;
}

export function getCurrentYear(): number {
    return new Date().getFullYear();
}

export function equals(fisrt: CalendarDate, second: CalendarDate) {
    return fisrt.year === second.year &&
        fisrt.month === second.month &&
        fisrt.day === second.day;
}

export default {
    getIterableMonth,
    getDaysInMonth,
    getDayInWeek,
    getMonthNameByIndex,
    getWeeksCountInMonth,
    getCurrentYear,
    equals,
    getWeekdays,
};
