import React, {useEffect} from 'react';
import styled from 'styled-components';
import {useStore} from 'effector-react';
import {Calendar, CalendarApi} from 'src/features/calendar';
import {DayComponentProps, MonthHeaderComponentProps, WeekDayComponentProps} from 'src/features/calendar/month';
import {CalendarDay, MonthTitle, ThemeToggle} from 'src/ui';
import {Theme} from 'src/ui/theme';
import {$goals, addGoal, loadGoalsFromLS, removeGoal} from 'src/store/goals';
import {$theme, setTheme} from 'src/store/theme';

const AppContainer = styled('div')`
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 35px;
  max-width: 1200px;
  min-height: 100vh;
  margin: 0 auto;
  box-sizing: border-box;
`;

const ControlPanel = styled('div')`
  padding: 15px;
  background-color: var(--background);
  position: fixed;
  top: 0;
  right: 0;
  transition: background-color .7s ease-in-out;
`;

const YearTitle = styled('h1')`
  margin-top: 10px;
  font-size: 3rem;
  color: var(--accent);
  font-family: Merriweather, sans-serif;
`;

const Footer = styled('footer')`
  position: absolute;
  bottom: 0;
  left: 0;
  height: 35px;
  font-size: 13px;
  text-align: center;
  padding-left: 15px;
`;

export const App: React.FC = () => {
    const year = CalendarApi.getCurrentYear();
    const theme = useStore($theme);

    useEffect(() => {
        loadGoalsFromLS();
    }, []);

    const handleChangeTheme = (value: boolean) => {
        value ? setTheme(Theme.light) : setTheme(Theme.dark);
    };

    return (
        <AppContainer>
            <ControlPanel>
                <ThemeToggle
                    id="theme"
                    value={theme === Theme.light}
                    onChange={handleChangeTheme}
                />
            </ControlPanel>
            <YearTitle>{year}</YearTitle>
            <Calendar
                year={year}
                WeekDayComponent={WeekDayComponent}
                DayComponent={DayComponent}
                MonthHeaderComponent={MonthHeader}
            />
            <Footer>Â© 2020 Antsiferov Maxim</Footer>
        </AppContainer>
    );
};

const WeekDayContainer = styled('div')`
  text-align: center;
  font-size: .6rem;
  font-weight: bold;
  width: 1.3rem;
  color: var(--text);
  transition: color .7s ease-in-out;
`;

const WeekDayComponent: React.FC<WeekDayComponentProps> = ({title}) => {
    return (
        <WeekDayContainer>{title}</WeekDayContainer>
    )
};

const DayComponent: React.FC<DayComponentProps> = ({date}) => {
    const goals = useStore($goals);
    if (date.day === 0) {
        return <CalendarDay isEmpty={true}/>
    }
    const now = new Date();
    const isCurrentDay = now.getDate() === date.day && now.getMonth() === date.month;
    const complete = goals.find(item => CalendarApi.equals(item.date, date)) !== undefined;
    const handlePress = () => {
        if (date.day > now.getDate() || date.month > now.getMonth()) {
            return;
        }

        complete ? removeGoal(date) : addGoal(date);
    };

    return (
        <CalendarDay
            isCurrentDay={isCurrentDay}
            complete={complete}
            onClick={handlePress}
        >
            {date.day}
        </CalendarDay>
    );
};

const MonthHeader: React.FC<MonthHeaderComponentProps> = props => (
    <MonthTitle>{CalendarApi.getMonthNameByIndex(props.date.month)}</MonthTitle>
);
