import React from 'react';
import range from 'lodash/range';
import styled from 'styled-components';

import {Month, DayComponentProps, MonthHeaderComponentProps, WeekDayComponentProps} from './month';
import {getMonthNameByIndex} from "./calendar-api";

const CalendarContainer = styled('div')`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
`;

const MonthContainer = styled('div')`
  padding: 15px;
`;

interface CalendarProps {
    year: number;
    WeekDayComponent?: React.FC<WeekDayComponentProps>
    MonthHeaderComponent?: React.FC<MonthHeaderComponentProps>,
    DayComponent?: React.FC<DayComponentProps>
}

const DayComponentDefault: React.FC<DayComponentProps> = ({date}) => {
    return (
        date.day === 0 ? <div/> : <div>{date.day}</div>
    )
};

const MonthHeaderComponentDefault: React.FC<MonthHeaderComponentProps> = (props) => {
    return (
        <h2>{getMonthNameByIndex(props.date.month)}</h2>
    )
};

export const Calendar: React.FC<CalendarProps> = (props) => {
    const {
        year,
        WeekDayComponent,
        MonthHeaderComponent = MonthHeaderComponentDefault,
        DayComponent = DayComponentDefault,
    } = props;

    return (
        <CalendarContainer>
            {range(0, 12).map(month => (
                <MonthContainer key={month}>
                    <Month
                        WeekDayComponent={WeekDayComponent}
                        MonthHeaderComponent={MonthHeaderComponent}
                        DayComponent={DayComponent}
                        year={year}
                        month={month}
                    />
                </MonthContainer>
            ))}
        </CalendarContainer>
    )
};
