export enum Color {
    background = 'background',
    text = 'text',
    accent = 'accent',
    secondary = 'secondary',
    accentText = 'accentText',
}

export enum Theme {
    light = 'light',
    dark = 'dark'
}

type ItemMap<C extends object, T> = { [P in keyof C]: T };

export const dark: ItemMap<typeof Color, string> = {
    background: '#000',
    text: '#fff',
    accent: '#ef2c2c',
    secondary: '#35363a',
    accentText: '#fff',
};

export const light: ItemMap<typeof Color, string> = {
    background: '#fff',
    text: '#000',
    accent: '#ef2c2c',
    secondary: '#b8babc',
    accentText: '#fff',
};

export const themes: ItemMap<typeof Theme, ItemMap<typeof Color, string>> = {
    dark,
    light,
};


export function setTheme(theme: Theme) {
    const html = document.getElementsByTagName('html')[0];
    const keys = Object.keys(themes[theme]);

    keys.forEach((key) => {
        const color = (themes[theme] as any)[key];

        html.style.setProperty(`--${key}`, color);
    });
}

export function initTheme(theme: Theme) {
    const html = document.getElementsByTagName('html')[0];
    html.style.transitionDuration = '0';

    setTheme(theme);

    html.style.transitionDuration = '0.7s';
}
