import {createDomain, forward} from 'effector';
import {CalendarDate} from 'src/features/calendar/calendar-api';
import {CalendarApi} from "../features/calendar";

export const GoalsDomain = createDomain();
const GOALS_LS_KEY = 'GOALS_LS_KEY';

export interface Goal {
    date: CalendarDate;
}

export const addGoal = GoalsDomain.event<CalendarDate>();
export const removeGoal = GoalsDomain.event<CalendarDate>();

export const loadGoalsFromLS = GoalsDomain.effect<void, Goal[]>()
    .use(() => {
        const payload = localStorage.getItem(GOALS_LS_KEY);

        if (!payload) {
            return [];
        }

        return JSON.parse(payload);
    });
export const saveGoalsToLS = GoalsDomain.effect<Goal[], void>()
    .use(goals => {
        localStorage.setItem(GOALS_LS_KEY, JSON.stringify(goals));
    });

export const $goals = GoalsDomain.store<Goal[]>([])
    .on(addGoal, (state, payload) => (
        [...state, {date: payload}])
    )
    .on(removeGoal, (state, payload) => state.filter(item => !CalendarApi.equals(item.date, payload)))
    .on(loadGoalsFromLS.done, (state, {result}) => result);

forward({
    from: $goals,
    to: saveGoalsToLS,
});
