import {createDomain, forward} from 'effector';

import {setTheme as setT, initTheme, Theme} from 'src/ui/theme';

const THEME_LS_KEY = 'THEME_LS_KEY';

const ThemeDomain = createDomain();

export const setTheme = ThemeDomain.event<Theme>();
export const updateTheme = ThemeDomain.effect<Theme, void>()
    .use(params => {
        setT(params);
        localStorage.setItem(THEME_LS_KEY, params);
    });

const defaultStore = (localStorage.getItem(THEME_LS_KEY) as any) || Theme.dark;
initTheme(defaultStore);

export const $theme = ThemeDomain
    .store<Theme>(defaultStore)
    .on(setTheme, (state, payload) => payload);

forward({
    from: $theme,
    to: updateTheme,
});
