import React, {useEffect, useState} from "react";
import styled from 'styled-components';
import {CalendarDate, IterableMonth, getIterableMonth, getWeekdays} from './calendar-api';

const MonthContainer = styled('div')`
  display: flex;
  flex-direction: column;
`;

const Week = styled('div')`
  display: flex;
`;

export interface DayComponentProps {
    date: CalendarDate;
    index: number;
}

export interface MonthHeaderComponentProps {
    date: CalendarDate;
    index: number;
}

export interface WeekDayComponentProps {
    title: string;
    date: CalendarDate;
    index: number;
}

interface MonthProps {
    year: number;
    month: number;
    WeekDayComponent?: React.FC<WeekDayComponentProps>,
    DayComponent: React.FC<DayComponentProps>;
    MonthHeaderComponent: React.FC<MonthHeaderComponentProps>,
}

export const Month: React.FC<MonthProps> = (props) => {
    const {year, month,
        WeekDayComponent,
        MonthHeaderComponent,
        DayComponent,
    } = props;
    const [weeks, setWeeks] = useState<IterableMonth>([]);

    useEffect(() => {
        setWeeks(getIterableMonth(year, month));
    }, []);

    return (
        <MonthContainer>
            <MonthHeaderComponent index={month} date={{year, month, day: 0}}/>
            {WeekDayComponent && <Week>
                {getWeekdays().map((title, index) => (
                    <WeekDayComponent date={{year, month: index, day: 0}} key={index} title={title} index={index}/>
                ))}
            </Week>}
            {weeks.map((week: number[], weekIndex: number) => (
                <Week key={weekIndex}>
                    {week.map((day, dayIndex) => (
                        <DayComponent
                            index={dayIndex}
                            date={{year, month, day}}
                            key={dayIndex}
                        />
                    ))}
                </Week>
            ))}
        </MonthContainer>
    )
};
